export function getStringResource(key, env: ENV = 'zh'): string {
  if (env === 'zh') {
    return zhKeyMap[key];
  }
  return enKeyMap[key];
}

export function f(key, env: ENV = 'zh'): string {
  return getStringResource(key, window?.tripdocs?.lang || env);
}
